.footer {
	clear: both;
	background: $black;
	overflow: hidden;
	text-transform: uppercase;

	ul.ftrLinks {
		float: left;

		li {
			float: left;
			margin-right: 20px;

			a {
				line-height: 40px;
				color: $white;
				@include transitionspeed(.3s);

				&:hover {
					color: $red; } }

			@include formobile {
				float: none;
				clear: both; } }

		@include formobile {
			float: none; } }

	@include fortablet {
		padding: 0 10px;
		box-sizing: border-box; }

	@include formobile {
		padding: 0 10px;
		box-sizing: border-box;
		text-align: center; }

	.copyright {
		float: right;
		color: $white;

		p {
			line-height: 40px; }

		@include formobile {
			float: none;
			clear: both; } } }
