.carouselScheduleBlock {
	overflow: hidden;
	clear: both;
	background: $darkGray;
	h1 {
		width: 120px;
		float: left;
		color: $white;
		font-size: 21px;
		line-height: 90px;
		text-transform: uppercase;
		text-align: center; }

	.carouselSchedule {
		//width: 90%
		@include calcu(max-width,'100% - 120px');
		float: left;
		height: 90px;
		.csBox {
			float: left;
			@include calcu(max-width,'100% - 60px');
			.dateBox {
				width: 70px;
				float: left;
				text-align: center;
				position: relative;
				background: $white1;
				padding: 10px 0;

				span {
					display: block;
					color: $lightGray;
					@include font($light, 14px, normal);
					text-transform: uppercase;
					line-height: 18px;
					padding: 3px 0;

					&.date {
						@include font($default, 21px, normal); } } }

			.league {
				float: left;
				background: $white2;
				height: 89px;
				border-top: 1px $white2 solid;

				&.single {
					width: 130px;
					& > div {
						width: 100%; } }
				&.double {
					width: 260px;
					& > div {
						width: 50%;
						position: relative;
						&:first-of-type {
							&:before {
								content: '';
								display: block;
								position: absolute;
								top: 0;
								right: 0;
								height: 90px;
								width: 1px;
								background: $white2; } } } }
				.game {
					float: left;


					h2 {
						background: $gray;
						padding: 5px;
						color: $white; }
					ul {
						li {
							width: 56px;
							float: left;
							text-align: center;
							padding: 5px 0 0;
							position: relative;
							.logoBox {
								width: 90%;
								height: 35px;
								overflow: hidden;
								margin: 0 auto;
								img {
									width: 100%;
									height: auto;
									display: block; } }
							p {
								line-height: 23px;
								padding-top: 3px;
								span {
									padding-left: 5px;
									&.win {
										color: $red; } } }

							&.vs {
								width: 18px;
								p {
									line-height: 60px;
									text-transform: uppercase;
									padding: 0; } } } } } } } }

	.slick-arrow {
		z-index: 2;
		cursor: pointer;
		color: $white;
		background: $dirtyWhite;
		@include transitionspeed(.3s);
		width: 30px;
		height: 90px;
		position: relative;

		i {
			@include centerAll(); }

		&:hover {
			opacity: .9;
			@include transitionspeed(.3s); }

		&.prevBtn {
			float: left; }

		&.nextBtn {
			float: right; } } }
