@include forresponsive() {

	//Inner Contents GLobal
	.miniBlock {
		&.by2s {
			li {
				width: 100%; } }
		li {
			float: none;
			margin-right: 0;
			.imgBlock {
				img {
					width: 100%;
					height: auto; } } } }

	.mobImg {
		@include showMob(); }
	.defImg {
		@include showTab(); }

	//Header
	.top-section {
		background: $red;
		border-bottom: 0;
		max-height: 1000px;

		ul.topnav {
			display: none; }

		.moreNav {
			display: block;
			padding: 0 10px;
			border-right: 1px $maroon solid;
			float: left;
			color: $white;
			cursor: pointer;

			&:hover,
			&:focus,
			&.show {
				background: $darkAqua; } }

		.backHome {
			display: block;
			padding: 0 10px;
			border-right: 1px $maroon solid;
			float: left;
			color: $white;
			cursor: pointer;

			&:hover,
			&:focus,
			&.show {
				background: $darkAqua; } }

		.moreNavContents {
			max-height: 0;
			overflow: hidden;
			display: block;
			clear: both;
			border-top: 1px $maroon solid;
			border-bottom: 1px $maroon solid;
			@include transitionspeed(.3s);

			&.show {
				@include transitionspeed(.3s);
				max-height: 1000px; }

			li {
				border-bottom: 1px $darkAquaDarken solid;

				&:last-of-type {
					border-bottom: 0; }

				a {
					color: $white;
					padding: 0 10px;
					background: $darkAqua;
					display: block;
					span {
						float: right;
						@include transitionspeed(.3s); }

					&.live {
						i {
							color: $liveGreen; } }

					&:hover {
						background: $darkAquaDarken; } }

				&.more {
					ul {
						max-height: 0;
						overflow: hidden;
						border-top: 1px $darkAquaDarken solid;
						@include transitionspeed(.3s);

						li {
							a {
								color: $white;
								padding: 0 10px 0 20px; } } }

					&.show {
						ul {
							max-height: 1000px; } }

					&.show {
						a {
							span {
								@include rotateX(180deg); } } } } } } }


	.mid-section {
		&.show {
			max-height: 1336px; }
		.logo {
			margin-left: 10px; }
		.triggers {
			display: block;
			position: absolute;
			color: $white;
			cursor: pointer;
			@include transitionspeed(.3s);
			right: 0;

			& > div {
				position: relative;
				height: 80px;
				width: 25px;
				float: right;
				margin-right: 10px;

				&.show,
				&:hover {
					color: $red;
					@include transitionspeed(.3s); }

				i {
					@include centerVert(); } } } }

	//Footer
	.footer {
		ul.ftrLinks {
			li {
				a {}
				@include formobile {
					float: none;
					clear: both; } }
			@include formobile {
				float: none; } }
		@include fortablet {
			box-sizing: border-box; }
		@include formobile {
			box-sizing: border-box;
			text-align: center; }

		.copyright {
			p {
				line-height: 40px; }

			@include formobile {
				float: none;
				clear: both; } } } }
