.contBlock {
	padding: 25px 0;
	float: left;
	width: 100%;
	position: relative;
	&.light {
		background: $lightGray; }
	&.dark {
		background: $darkGray; }

	.titleBox {
		h2 {
			@include font($default, 36px, normal);
			text-transform: uppercase;
			padding: 2px 0 0;
			color: $white;
			float: left; }
		a {
			&.moreBtn {
				color: $white;
				background: $red;
				display: block;
				float: left;
				padding: 10px 20px 7px;
				text-transform: uppercase;
				margin-left: 10px;
				@include font($default, 14px, normal);
				&:hover {
					background: $white;
					color: $red; } } } } }


.mobImg {
	@include showTabMob(); }

.wrap {
	padding: 25px 0 0;
	overflow: hidden;
	clear: both; }

.sideAds {
	float: none;
	width: 100%;
	padding: 25px;
	box-sizing: border-box;
	background: $white1;
	img {
		border: 1px $lighterGray solid;
		width: 100%;
		height: auto; } }

.leftPanel {
	float: left;
	@include calcu(max-width,'100% - 325px');
	width: 100%; }

.rightPanel {
	float: right;
	max-width: 300px;
	width: 100%; }

.colBlock {
	width: 100%;
	overflow: hidden;
	position: relative;
	margin-bottom: 25px;

	&:hover {
		.imgBlock {
			img {
				@include transitionspeed(8s);
				@include transformstyle(ease-in-out);
				@include scale(1.2); } } }

	.imgBlock {
		overflow: hidden;
		img {
			@include scale(1.1);
			width: 100%;
			@include transitionspeed(8s);
			@include transformstyle(ease-in-out); } }

	.captionBlock {
		background: url('../images/blackTransparent.png');
		padding: 15px;
		position: absolute;
		bottom: 0;
		width: 100%;
		text-transform: uppercase;
		box-sizing: border-box;
		.texts {
			float: left;
			@include calcu(max-width,'100% - 180px');
			h3 {
				@include font($default, 30px, normal);
				color: $white;
				padding-bottom: 5px; }
			p {
				color: $white;
				@include font($default, 18px, normal); } }
		a {
			background: $red;
			color: $white;
			display: block;
			float: right;
			padding: 20px;
			@include font($default, 18px, normal);
			@include transitionspeed(.3s);
			text-align: center;
			&:hover {
				background: $white;
				color: $red; } } } }
.miniBlock {
	&.by2s {
		li {
			width: 48.59%; } }
	li {
		float: left;
		margin-right: 25px;

		&:last-of-type {
			margin-right: 0; }
		.texts {
			h3.minis {
				font-size: 18px;
				line-height: 21px;
				padding: 0; } }
		.readMoreLink {
			max-width: none;
			line-height: 40px;
			padding: 0 20px; } } }
