//SMALL SCREENS
@mixin desktopS {
    @media only screen and (min-width: 1024px) and (max-width: 1366px) {
        @content; } }

@mixin fordesktop {
    @media only screen and (min-width: 1024px) {
        @content; } }
//TABLET


@mixin ipadorient($orient) {
    @media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: $orient) {
        @content; } }

@mixin fortablet {
    @media only screen and (min-width: 768px) and (max-width: 1023px) {
        @content; } }

@mixin forstablet {
    @media only screen and (min-width: 601px) and (max-width: 767px) {
        @content; } }

//MOBILE
@mixin iphone {
    @media only screen and (min-device-width : 320px) and (max-device-width : 667px) and (-webkit-min-device-pixel-ratio: 2) {
        @content; } }

@mixin iphoneorient($orient) {
    @media only screen and (min-device-width : 375px) and (max-device-width : 667px) and (orientation: $orient) {
        @content; } }

@mixin formobileO($orient) {
    @media only screen and (min-width: 0) and (max-width: 600px) and (orientation: $orient) {
        @content; } }

@mixin formobile {
    @media only screen and (min-width: 0) and (max-width: 767px) {
        @content; } }

//TARGET < 1023
@mixin forresponsive {
    @media only screen and (max-width: 1023px) {
        @content; } }

//ALL FIREFOX TARGET
@mixin firefox {
    @-moz-document url-prefix() {
        @content; } }
